.error-enter {
  transform: translateX(+100%);
}

.error-enter-active {
  transform: translateX(0%);
  transition: transform 250ms linear;
}

.error-exit {
  transform: translateX(0%);
}

.error-exit-active {
  transform: translateX(+100%);
  transition: transform 250ms linear;
}

.fade-appear {
  transform: translateX(-100%);
}

.fade-appear-active {
  transform: translateX(0%);
  transition: transform 500ms linear;
}

.fade-enter {
  opacity: 0;
  transform: scale(0.1);
}

.fade-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 250ms linear;
}

.fade-exit {
  opacity: 1;
  transform: scale(1);
}

.fade-exit-active {
  opacity: 0;
  transform: scale(0.1);
  transition: transform 500ms linear;
  transition: all 250ms linear;
}
